<template>
  <div>
    <el-button
      class="add-button-add"
      size="small"
      type="primary"
      @click="dialogFormVisible = true"
      >添加</el-button
    >
    <el-dialog title="添加信息" :visible.sync="dialogFormVisible">
      <el-form :v-model="form">
        <el-form-item label="库位">
          <el-input v-model="form.location"></el-input>
        </el-form-item>
        <el-form-item label="绑定ip">
          <el-input v-model="form.ip"></el-input>
        </el-form-item>
        <el-form-item label="库位类型">
          <el-select v-model="form.type" placeholder="请选择库位类型"  style="float: left;">
            <el-option label="放货库位" value="deliveryLocation"></el-option>
            <el-option label="取货库位" value="takeLocation"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="clear">取 消</el-button>
        <el-button type="primary" @click="onSubmit">添加</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";
export default {
  data() {
    return {
      dialogFormVisible: false,
      form: {
        location: "",
        ip: "",
        type: "",
        createTime: "",
      },
    };
  },
  methods: {
    clear() {
      this.form = {
        location: "",
        ip: "",
        type: "",
        createTime: "",
      };
      this.dialogFormVisible = false;
    },
    onSubmit() {
      var date1 = dayjs(new Date().getTime() - 24 * 60 * 60 * 1000).format(
        "YYYY-MM-DD HH:mm:ss"
      );
      this.$axios
        .post("/web/location/add", {
          location: this.form.location,
          ip: this.form.ip,
          type: this.form.type,
          createTime: date1,
        })
        .then((resp) => {
          if (resp && resp.data.code === 200) {
            this.$emit("onSubmit");
          }
          this.clear();
          this.$emit("refresh");
        });
    },
  },
};
</script>

<style>
.add-button-add {
  width: 74px;
  height: 32px;
}
</style>


